/*
    general style
 */

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

html {
  height: 100%;
}

body {
  font-family: $hr, sans-serif;
  font-size: 20px;
  line-height: 1;
  color: #555;
  background: #fff;

  @include mq(xs) {
    font-size: 18px;
  }
}

.container {
  width: 1200px;
  margin: 0 auto;

  @include mq(xl) {
    width: 1140px;
  }

  @include mq(lg) {
    width: 960px;
  }

  @include mq(md) {
    width: 720px;
  }

  @include mq(s) {
    width: 94%;
  }

  @include mq(xs) {
    width: 90%;
    min-width: 290px;
  }
}

a {
  display: block;
  text-decoration: none;
  text-align: center;

  &:active, &:focus {
    outline: none;
  }
}

img {
  display: block;
  margin: 0 auto;
  object-fit: cover;
  user-select: none;
}

h1, h2, h6 {
  font-family: $hb, sans-serif;
}

h1, h2 {
  font-size: 55px;
  color: #fff;
  text-transform: uppercase;

  @include mq(s) {
    font-size: 48px;
  }

  @include mq(xs) {
    font-size: 36px;
  }
}

h3 {
  font-family: $hr, sans-serif;
  font-size: 36px;
  color: #190f46;

  @include mq(s) {
    font-size: 30px;
  }

  @include mq(xs) {
    font-size: 24px;
  }
}

h6 {
  font-size: 16px;
  color: #c73e55;
  text-transform: uppercase;
}

span {
  font-family: $hm, sans-serif;
  font-size: 30px;
  color: #fff;

  @include mq(s) {
    font-family: $hr, sans-serif;
    font-size: 25px;
  }

  @include mq(xs) {
    font-size: 20px;
  }

  &.sale {
    font-family: $hb, sans-serif;
    font-size: 96px;
    color: #fff500;
    text-align: center;
    display: block;

    @include mq(s) {
      font-size: 75px;
    }

    @include mq(xs) {
      font-size: 55px;
    }
  }
}

p {

  @include mq(xs) {
    line-height: 20px;
  }

  &.small {
    font-size: 18px;
  }
}

.to-order {
  color: #fff;
  text-align: center;
  max-width: 350px;

  a {
    font-family: $hb, sans-serif;
    font-size: 24px;
    color: #fff;
    text-transform: uppercase;
    box-sizing: border-box;
    width: 100%;
    padding: 25px 0 15px;
    border: 3px solid #fff;
    background: transparent;
    transition: all 0.55s ease-in-out;

    &:hover {
      background: #fff;
      color: #c73e55;
    }
  }

  h6 {
    text-transform: uppercase;
    margin-top: 14px;

    @include mq(xs) {
      font-size: 15px;
    }
  }
}

a.order, .form-group.order button {
  font-family: $hb, sans-serif;
  font-size: 20px;
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  width: 300px;
  padding: 21px 0 14px;
  background: #fa4748;
  border-radius: 5px;
  transition: all 0.55s ease-in-out;

  &:hover {
    background: #190f46;
  }
}

.gallery, .photo {
  background: linear-gradient(180deg, #2e227c 0%, #fb4747 100%);
  padding: 26px 0;

  @include mq(md) {
    padding: 20px 0;
  }

  &__content {
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;

    @include mq(s) {
      display: block;
    }
  }
}

.gallery__content, .photo__content, .catalog__block-img {

  a {
    position: relative;

    img {
      transition: all 0.5s ease-in-out;

      @include mq(xxl) {
        width: 100%;
      }
    }

    &:after {
      content: "";
      display: block;
      width: 80px;
      height: 80px;
      background: url("../img/zoom.png") no-repeat center/cover;
      opacity: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: all 0.85s ease-in-out;
    }

    &:hover {

      img {
        opacity: .3;

        @include mq(md) {
          opacity: 1;
        }
      }

      &:after {
        opacity: 1;

        @include mq(md) {
          display: none;
        }
      }
    }
  }
}

.fade {
  opacity: 0;
  transition: all 0.55s ease-in-out;

  &.animated {
    opacity: 1;
  }
}

.body-filter {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: -5;
}